/******************************************************************
GLOBAL BASE STYLES
Opinionated base styles to start building upon
******************************************************************/


*, *:before, *:after {
    box-sizing: border-box;
}

/*@media (-webkit-device-pixel-ratio: 1.25) {
    * {
        zoom: 0.75;
    }
}*/

::selection {
    color: $color-selection;
    background: $color-selection-bg;
}

::placeholder {
    color: $color-placeholder !important;
}

HTML {
    scroll-behavior: smooth;
    font-family: $font-family-main;
    font-size: $font-size-calc-base;
    font-weight: $font-weight-normal;
    line-height: $line-height-base;
    color: $color-text;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

BODY {
    font-size: $font-size-p;

    @include below-desktop {
        font-size: 1.6rem;
    }

    @include below-mobile {
        font-size: 1.4rem;
    }
}

MAIN {
    //overflow-x: hidden;
}

P {
    margin: 0 0 24px 0;
}

H1, H2, H3, H4, H5, H6, .h1, .h2, .h3, .h4, .h5 {
    font-weight: $font-weight-medium;
    font-family: $font-family-headings;
    margin-top: 0;
}
H1, .h1 {
    font-size: $font-size-h1;
    line-height: $line-height-sm;

    @include below-mobile {
        font-size: $font-size-h2;
    }

    @include below-mobile-sm {
        font-size: $font-size-h3;
    }
}
H2, .h2 {
    font-size: $font-size-h2;
    line-height: $line-height-sm;

    @include below-mobile {
        font-size: $font-size-h3;
    }
}
H3, .h3 {
    font-size: $font-size-h3;
    line-height: $line-height-sm;

    @include below-mobile {
        font-size: 3.2rem;
    }
}
H4, .h4 {
    font-size: $font-size-h4;
}
H5, .h5 {
    font-size: $font-size-h5;
}

A {
    color: $color-link;
    transition: color $trans-time $trans-type;

    &:hover {
        color: $color-hover;
        transition: color $trans-time $trans-type;
    }
}

BUTTON, INPUT[type="submit"] {
	appearance: none;
	display: inline-block;
	position: relative;
	outline: 0;
	border: 0;
	border-radius: 0;
	background: 0;
	text-decoration: none;
	color: $color-link;
	transition: color $trans-time $trans-type;

	&:hover {
		color: $color-hover;
		transition: color $trans-time $trans-type;
		cursor: pointer;
	}

	&:active, &:focus {
		outline: 0;
	}
}

IMG {
    display: block;
}

UL {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

FIGURE {
    margin: 0 0 24px 0;
}

FIELDSET {
    padding: 0;
    margin: 0;
    border: 0;
}

LEGEND {
    margin-bottom: 24px;
}

ABBR[title] {
    text-decoration: none;
}

HR {
    box-sizing: border-box;
    border: none;
}

TABLE {
    width: 100%;
    border-collapse: collapse;

    TH, TD {
        text-align: left;
        border: 2px solid $color-border02;
        padding: 16px 20px;
    }

    TH {
        background: $color02-light;
        font-weight: $font-weight-medium;
    }
}

STRONG {
    font-weight: $font-weight-medium;
}

BLOCKQUOTE {
    position: relative;
    appearance: none;
    margin: 24px 0;
    padding: 0 40px 0 60px;
    //background: $color-white;

    &:before {
        content: "";
        position: absolute;
        width: 34px;
        height: 28px;
        background: url('../../assets/icons/icon-quote.svg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
        z-index: 0;
    }

    &:before {
        top: 0;
        left: 1px;
    }

    CITE {
        margin-top: 36px;
        display: block;
        font-weight: $font-weight-medium;
        font-style: normal;

        &:before {
            content: "\2014";
            margin-right: 8px;
            font-weight: $font-weight-normal;
        }
    }

    *:first-child {
        margin-top: 0;
    }
    *:last-child {
        margin-bottom: 0;
    }

    @include below-mobile-xs {
        padding: 48px 24px;
    }
}

CODE {
    display: block;
    width: 100%;
    padding: 16px 24px;
    background: $color-gray-light;
}

MARK {
    background-color: $color-warning;
    color: $color01;
    padding: 2px 4px;
}