/******************************************************************
GLOBAL STATES
Various global helper states to be used throughout the project
Always prefix with "is-"
******************************************************************/


.is-hidden {
    display: none;
}

.is-shown {
    display: block;
}

.is-invisible {
    visibility: hidden;
}

.is-visible {
    visibility: visible;
}

.is-fixed {
    position: fixed;
}

.is-sticky {
    position: sticky;
}