/******************************************************************
BUTTON STYLES
All button styles, starts with a default baseline
******************************************************************/


.button {
	position: relative;
	display: inline-block;
	background: $color01;
	padding: 16px 40px;
	min-width: 200px;
	min-height: 60px;
	color: $color-white;
	font-size: 18px; // Static font height so buttons are not affected by global scaling
	font-weight: $font-weight-normal;
	text-decoration: none;
	text-align: center;
	transition: $transition-default;
	cursor: pointer;
	overflow: hidden;

	&:after {
		content: "";
		position: absolute;
		bottom: calc(50% - 150px);
		left: calc(50% - 150px);
		width: 300px;
		height: 300px;
		border-radius: 100%;
		background: rgba($color-white, 0.1);
		transform: scale(0%);
		transition: $transition02;
	}

	&:hover, &:focus {
		color: $color-white;
		background: $color01-light;
		//transform: translateY(-2px);
		box-shadow: $shadow02;
		transition: $transition-default;

		&:after {
			transform: scale(100%);
			transition: $transition01;
		}
	}

	/*&:active {
		transform: scale(0.98);
		transition: $transition01;
	}*/

	@include below-mobile-sm {
		width: 100%;
		max-width: 100%;
		min-width: 200px;
		text-align: center;
	}

	// Color variations
	&--color-accent {
		background: $color03;

		&:hover, &:focus {
			background: $color03-light;
		}
	}

	&--color-light {
		background: $color02;
		color: $color-text;

		&:hover, &:focus {
			background: $color02-light;
			color: $color-text;
		}
	}

	// Shape variations {
	&--round {
		border-radius: 30px;
	}

	// Animations
	&--icon-animated {
		@extend .has-animated-icon-right;
	}

	// Size variations
	&--size-sm {
		padding: 8px 30px;
		min-width: 48px;
		min-height: 48px;
	}

	&--wide {
		min-width: 240px;
	}

	// Icon variations
	&--icon-left, &--icon-right {
		position: relative;

		.icon {
			width: 24px;
			height: 24px;
			position: absolute;
			top: calc(50% - 12px);
			fill: $color-white;
		}
	}
	&--icon-left {
		padding-left: 76px;
		.icon {
			left: 36px;
		}
	}
	&--icon-right {
		padding-right: 76px;
		.icon {
			right: 36px;
		}
	}
	&--icon-only {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 60px;
		height: 60px;
		padding: 0;
		min-width: 60px;

		.icon {
			width: 24px;
			height: 24px;
			margin-bottom: 1px;
		}
		font-size: 0;
	}
	&--icon-only-sm {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 48px;
		height: 48px;
		padding: 0;
		min-width: 48px;
		min-height: 48px;

		.icon {
			width: 20px;
			height: 20px;
			margin-bottom: 1px;
		}
		font-size: 0;
	}
	&--icon-mobile-only {
		.icon {
			display: none;
		}

		@include below-mobile {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 56px;
			height: 56px;
			min-width: auto;
			padding: 0;

			.icon {
				display: block;
				width: 16px;
				height: 16px;
				margin-bottom: 1px;
			}
			font-size: 0;
		}
	}
	&--icon-mobile-only-bgless {
		.icon {
			display: none;
		}

		@include below-mobile {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 56px;
			height: 56px;
			min-width: auto;
			padding: 0;
			color: $color-text;
			background-color: transparent;

			.icon {
				display: block;
				width: 20px;
				height: 20px;
				margin-bottom: 1px;
			}
			font-size: 0;

			&:hover, &:active {
				color: $color-text;
				box-shadow: none;
				background-color: transparent;
			}
		}
	}

	// States
	&.is-disabled {
		opacity: $opacity-md;
		pointer-events: none;
		cursor: default;
	}
	&.is-loading {
		opacity: $opacity-md;
		pointer-events: none;
		cursor: wait;
	}
}

.play-button {
	position: relative;
	width: 80px;
	height: 80px;
	border-radius: 80px;
	background: $color03;
	color: $color-white;
	transition: $transition-default;
	overflow: hidden;

	&__icon {
		margin-top: 2px;
		margin-left: 7px;
		height: 28px;
		width: 28px;
	}

	+ .play-tooltip {
		position: absolute;
		top: -80px;
		left: calc(50% - 160px);
		width: 320px;
		opacity: 0;
		transform: translateY(2px);
		pointer-events: none;
		transition: $transition-default;
	}

	&:after {
		content: "";
		position: absolute;
		bottom: calc(50% - 60px);
		left: calc(50% - 60px);
		width: 120px;
		height: 120px;
		border-radius: 100%;
		background: rgba($color-white, 0.1);
		transform: scale(0%);
		transition: $transition02;
	}

	&:hover, &:focus {
		color: $color-white;
		background: $color03-light;
		box-shadow: $shadow02;
		transition: $transition-default;

		&:after {
			transform: scale(100%);
			transition: $transition01;
		}

		+ .play-tooltip {
			opacity: 1;
			transform: translateY(-8px);
			transition: $transition-default;
		}
	}

	&:active {
		transform: scale(0.98);
		transition: $transition01;
	}

	@include below-mobile {
		/*width: 56px;
		height: 56px;

		&__icon {
			height: 24px;
			width: 24px;
		}*/

		+ .play__tooltip {
			display: none;
		}
	}
}

.more-link {
	position: relative;
	font-size: $font-size-lead;
	font-weight: $font-weight-medium;
	text-decoration: none;
	padding: 0 0 8px 0;
	border-bottom: 2px solid $color-text;
	white-space: nowrap;
	line-height: 1;

	&:before {
		content: attr(data-text);
		position: absolute;
		left: 0;
		top: 1px;
		color: #8e9897; //rgba($color-text, $opacity-md);
		padding: 0 0 9px 0;
		border-bottom: 2px solid #8e9897;
		line-height: 1;
		overflow: hidden;
		width: 0%;
		transition: $transition02;
	}

	&:hover {
		//color: $color-text;

		&:before {
			width: 100%;
			transition: $transition02;
		}
	}

	@include below-mobile {
		font-size: 1.6rem;

		&:before {
			top: 1px;
		}
	}
}

.cart-button {
	display: block;
	position: relative;
	height: 28px;
	width: 28px;
	padding: 2px;

	&__amount {
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: -6px;
		right: -6px;
		width: 18px;
		height: 18px;
		border-radius: 18px;
		background: $color03;
		color: $color-white;
		font-size: $font-size-xs;
		font-weight: $font-weight-medium;
		transition: $transition-default;
		z-index: 1;
	}
}