/******************************************************************
SASS MIXINS
******************************************************************/

// MEDIA QUERIES
// For mobile media queries, mostly use below (includes upper boundary)
// For desktop media queries, mostly use above (includes lower boundary)
// Tablet is a range (mobile to desktop, excluding both boundaries)
@mixin not-supported {
	@media (max-width: #{$breakpoint-min - 1px}) {
		@content;
	}
}

@mixin above-min {
	@media (min-width: #{$breakpoint-min}) {
		@content;
	}
}

@mixin above-mobile-xs {
	@media (min-width: #{$breakpoint-mobile-xs + 1px}) {
		@content;
	}
}
@mixin below-mobile-xs {
	@media (max-width: #{$breakpoint-mobile-xs}) {
		@content;
	}
}

@mixin above-mobile-sm {
	@media (min-width: #{$breakpoint-mobile-sm + 1px}) {
		@content;
	}
}
@mixin below-mobile-sm {
	@media (max-width: #{$breakpoint-mobile-sm}) {
		@content;
	}
}

@mixin above-mobile {
	@media (min-width: #{$breakpoint-mobile + 1px}) {
		@content;
	}
}
@mixin below-mobile {
	@media (max-width: #{$breakpoint-mobile}) {
		@content;
	}
}

@mixin range-tablet {
	@media (min-width: #{$breakpoint-mobile + 1px}) and (max-width: #{$breakpoint-desktop - 1px}) {
		@content;
	}
}

@mixin above-desktop {
	@media (min-width: #{$breakpoint-desktop + 1px}) {
		@content;
	}
}
@mixin below-desktop {
	@media (max-width: #{$breakpoint-desktop}) {
		@content;
	}
}

@mixin above-desktop-lg {
	@media (min-width: #{$breakpoint-desktop-lg}) {
		@content;
	}
}
@mixin below-desktop-lg {
	@media (max-width: #{$breakpoint-desktop-lg - 1px}) {
		@content;
	}
}

@mixin above-desktop-xl {
	@media (min-width: #{$breakpoint-desktop-xl}) {
		@content;
	}
}
@mixin below-desktop-xl {
	@media (max-width: #{$breakpoint-desktop-xl - 1px}) {
		@content;
	}
}

@mixin above-desktop-xxl {
	@media (min-width: #{$breakpoint-desktop-xxl}) {
		@content;
	}
}
@mixin below-desktop-xxl {
	@media (max-width: #{$breakpoint-desktop-xxl - 1px}) {
		@content;
	}
}

@mixin above-desktop-uw {
	@media (min-width: #{$breakpoint-desktop-uw}) {
		@content;
	}
}
@mixin below-desktop-uw {
	@media (max-width: #{$breakpoint-desktop-uw - 1px}) {
		@content;
	}
}

@mixin below-max {
	@media (max-width: #{$breakpoint-desktop-max}) {
		@content;
	}
}


// TRANSITIONS
@mixin transition($target: all, $time: $trans-time, $type: $trans-type) {
	@if $time == 'short' {
		$time: $trans-time-sm;
	}
	@if $time == 'medium' {
		$time: $trans-time-md;
	}
	@if $time == 'long' {
		$time: $trans-time-lg;
	}

	transition: $target $time $type;
}