/******************************************************************
FORM STYLES
Styles for forms, both default and plugin-generated
******************************************************************/


::placeholder {
	color: $color-gray-light;
}

INPUT[TYPE="text"],
INPUT[TYPE="password"],
INPUT[TYPE="datetime"],
INPUT[TYPE="datetime-local"],
INPUT[TYPE="date"],
INPUT[TYPE="month"],
INPUT[TYPE="time"],
INPUT[TYPE="week"],
INPUT[TYPE="number"],
INPUT[TYPE="email"],
INPUT[TYPE="url"],
INPUT[TYPE="search"],
INPUT[TYPE="tel"],
INPUT[TYPE="color"],
SELECT,
TEXTAREA {
    display: block;
    height: 60px;
    width: 100%;
    min-width: 240px;
    max-width: 480px;
    padding: 12px 20px;
    margin-bottom: 16px;
    font-weight: $font-weight-normal;
    color: $color-text;
    vertical-align: middle;
    box-shadow: none;
    border: 2px solid $color-border02;
    border-radius: 0;
    background-color: $color-white;
	box-shadow: none;
    transition: $transition-default;

	&.has-no-border {
		border-color: $color-white;
	}

    &:focus,
    &:active {
		border: 2px solid $color01;
		outline: none;
		transition: $transition-default;
    }

    &[disabled],
    &.is-disabled {
		cursor: not-allowed;
		border-color: $color-disabled;
		opacity: 0.6;
    }
}

TEXTAREA {
	min-height: 180px;
	max-height: 360px;
	padding: 20px;
}

INPUT[TYPE="submit"] {
	@extend .button;
}

SELECT {
	appearance: none;
	position: relative;
	padding-right: 50px;
	background-image: url('../../assets/icons/icon-caron-down.svg');
	background-repeat: no-repeat;
	background-size: 14px;
	background-position: right 20px center;
	line-height: 1;
}

// Remove default dropdown arrow in IE
SELECT::-ms-expand {
	display: none;
}

LABEL {
	display: inline-block;
	margin-bottom: 10px;
	font-weight: $font-weight-normal;
	&:hover {
		cursor: pointer;
	}
}

INPUT[TYPE="RADIO"], INPUT[TYPE="CHECKBOX"] {
	appearance: none;

	position: relative;
	background: #fff;
	border: 2px solid $color01;
	width: 20px;
	height: 20px;
	cursor: pointer;
	vertical-align: middle;

	&:after {
		content: " ";
		display: block;
		position: absolute;
		top: 0; right: 0; bottom: 0; left: 0;
		width: 10px;
		height: 10px;
		margin: 3px;
		background: $color01;
		opacity: 0;
		transition: $transition-default;
	}

	&:active, &:focus {
		outline: 0;
	}

	&:checked {
		&:after {
			opacity: 1;
			transition: $transition-default;
		}
	}
}

INPUT[TYPE="RADIO"] {
	border-radius: 10px;

	&:after {
		border-radius: 5px;
	}
}

.text-field {
	position: relative;

	&__input {
		&:placeholder-shown {
			+ .text-field__placeholder {
				transform: translateY(8px);
				opacity: 0;
				transition: $transition-default;
			}
		}
	}

	&__placeholder {
		margin: 0;
		position: absolute;
		top: 5px;
		left: 22px;
		color: $color-gray;
		font-size: $font-size-xs;
		opacity: 1;
		transform: translateY(0);
		transition: $transition-default;
		pointer-events: none;
	}
}


.checkbox {
	display: inline-block;
	position: relative;

	&__input, .checkbox__input {
		position: absolute;
		left: 0;
		top: 5px;
	}

	&__label {
		padding: 3px 0 3px 32px;
		color: $color-text;
		margin-bottom: 0;
	}

	&.is-disabled {
		&__input {
			background: $color-disabled;
			cursor: not-allowed;
		}

		&__label {
			color: $color-gray-light;
			cursor: not-allowed;
		}
	}
}

.select {
	position: relative;

	&__input {
		padding-right: 60px;
		margin: 0;
	}

	&__icon {
		position: absolute;
		right: 30px;
		top: 29px;
		height: 10px;
		width: 16px;
		color: $color-gray;
		pointer-events: none;
	}
}

.radio-table {
	margin-bottom: 20px;

	.radio-table__title {
		margin-bottom: 8px;
	}

	&__heading, &__content {
		display: table-row;
	}

	&__heading {
		border-bottom: 1px solid $color-gray;
	}

	&__col {
		display: table-cell;
		padding: 10px;
		min-width: 64px;
		text-align: center;

		&:nth-child(odd) {
			background: $color-gray-light;
		}
	}

	@include below-mobile-xs {
		&__col {
			min-width: 42px;
			padding: 6px;
		}
	}
}

.input-group {
	position: relative;
	margin-bottom: 24px;
	font-size: $font-size-sm;

	&--checkbox, &--radio {
		.input-group__input {
			position: absolute;
			left: 0;
			top: 0;
			margin-right: 6px;
		}

		.input-group__label {
			vertical-align: middle;
			padding-left: 32px;
			margin-bottom: 0;
		}
	}
}

.hf-form {
	display: block;
	width: 100%;

	.hf-message {
		margin: 20px 0;
	}
	
	.hf-message-success {
		color: $color-positive;
	}

	.hf-message-error {
		color: $color-negative;
	}

	.hf-message-warning {
		color: $color-warning;
	}
}

.search-form {
	display: flex;

	.search-form__field {
		width: 100%;
	}

	&--header {
		margin: 0;
		
		.search-form__input {
			margin: 0;
			max-width: 100%;
			padding-left: 0;
	
			&:focus, &:active {
				border: 2px solid rgba($color01, 0);
			}
		}

		.search-form__label {
			left: 3px;
		}
	}
}