/******************************************************************
EDITOR
Styles for WordPress TinyMCE editor content output.
Always use a container with .editor-content class to output editor content.
******************************************************************/


.editor-content, .block-editor {
	line-height: $line-height-base;

	> *:first-child {
		margin-top: 0;
	}
	> *:last-child {
		margin-bottom: 0;
	}

	H1, H2, H3, H4, H5, H6 {
		margin: 20px 0;
	}

	H1, .h1, H2, .h2 {
		margin-top: 60px;
		font-size: 2.8rem;
		padding-bottom: 16px;
		border-bottom: 2px solid $color-border;
		text-transform: uppercase;
		letter-spacing: 1px;
	}

	P {
		margin: 24px 0;
	}

	UL, OL {
		margin: 2px 0 20px 0;
		padding-left: 0;
	}

	OL {
		list-style-position: inside;
	}

	UL {
		list-style-type: none;

		LI {
			position: relative;
			padding-left: 24px;
			margin-bottom: 8px;

			&:before {
				content: "";
				display: inline-block;
				position: absolute;
				left: 0;
				top: 8px;
				height: 8px;
				width: 8px;
				border-radius: 4px;
				background-color: $color01;
			}
		}
	}

	P + UL, P + OL {
		margin-top: -16px;
	}

	IMG {
		max-width: 100%;
		height: auto;
		margin-bottom: 30px;

		&.alignleft {
			display: inline;
			float: left;
			margin-right: 30px;
	
			@include below-mobile {
				display: block;
				float: none;
				margin: 0 auto;
			}
		}
	
		&.alignright {
			display: inline;
			float: right;
			margin-left: 30px;
	
			@include below-mobile {
				display: block;
				float: none;
				margin: 0 auto;
			}
		}
	}

    .wp-caption {
		width: 100%;
		max-width: 300px;
		height: auto;
		margin-bottom: 20px;
		line-height: 1.2;

		IMG {
			margin-bottom: 8px;
		}
		&.alignright {
			float: right;
			padding-left: 10px;
		}
		&.alignleft {
			float: left;
			padding-right: 10px;
		}
		&.aligncenter {
			margin: 0 auto;
		}
	}
	.wp-caption-text {
		//font-style: italic;
		color: #aaa;
		margin-top: 0;
		text-decoration: none !important;
	}

}