/******************************************************************
VARIABLES - TYPOGRAPHY
******************************************************************/

// Webfonts
@font-face {
    font-family: 'Simplon';
    src: url('../../assets/fonts/Simplon/Simplon-Light.woff2') format('woff2'),
        url('../../assets/fonts/Simplon/Simplon-Light.ttf') format('ttf');
    font-weight: 300;
    font-style: normal;
}
@font-face {
    font-family: 'Simplon';
    src: url('../../assets/fonts/Simplon/Simplon-LightItalic.woff2') format('woff2'),
        url('../../assets/fonts/Simplon/Simplon-LightItalic.ttf') format('ttf');
    font-weight: 300;
    font-style: italic;
}
@font-face {
    font-family: 'Simplon';
    src: url('../../assets/fonts/Simplon/Simplon-Regular.woff2') format('woff2'),
        url('../../assets/fonts/Simplon/Simplon-Regular.ttf') format('ttf');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'Simplon';
    src: url('../../assets/fonts/Simplon/Simplon-RegularItalic.woff2') format('woff2'),
        url('../../assets/fonts/Simplon/Simplon-RegularItalic.ttf') format('ttf');
    font-weight: 400;
    font-style: italic;
}
@font-face {
    font-family: 'Simplon';
    src: url('../../assets/fonts/Simplon/Simplon-Medium.woff2') format('woff2'),
        url('../../assets/fonts/Simplon/Simplon-Medium.ttf') format('ttf');
    font-weight: 500;
    font-style: normal;
}
@font-face {
    font-family: 'Simplon';
    src: url('../../assets/fonts/Simplon/Simplon-MediumItalic.woff2') format('woff2'),
        url('../../assets/fonts/Simplon/Simplon-MediumItalic.ttf') format('ttf');
    font-weight: 500;
    font-style: italic;
}
@font-face {
    font-family: 'Simplon';
    src: url('../../assets/fonts/Simplon/Simplon-Bold.woff2') format('woff2'),
        url('../../assets/fonts/Simplon/Simplon-Bold.ttf') format('ttf');
    font-weight: 700;
    font-style: normal;
}
@font-face {
    font-family: 'Simplon';
    src: url('../../assets/fonts/Simplon/Simplon-BoldItalic.woff2') format('woff2'),
        url('../../assets/fonts/Simplon/Simplon-BoldItalic.ttf') format('ttf');
    font-weight: 700;
    font-style: italic;
}


// Fonts
$font-family-main:      Simplon, Helvetica, Arial, sans-serif;
$font-family-headings:  Simplon, Helvetica, Arial, sans-serif;
$font-family-accents:   Simplon, Helvetica, Arial, sans-serif;


// Sizes
$font-size-calc-base:   62.5%;
$font-size-base:        1.8rem;

$font-size-xxl:         4.8rem;
$font-size-xl:          4.2rem;
$font-size-lg:          3.6rem;
$font-size-md:          2.4rem;
$font-size-sm:          1.4rem;
$font-size-xs:          1.2rem;

$font-size-h1:          $font-size-xxl;
$font-size-h2:          $font-size-xl;
$font-size-h3:          $font-size-lg;
$font-size-h4:          $font-size-md;
$font-size-h5:          $font-size-base;
$font-size-lead:        2.0rem;
$font-size-p:           $font-size-base;


// Line heights
$line-height-base:      1.5;
$line-height-sm:        1.34;
$line-height-lg:        1.8;


// Letter spacing
$letter-spacing-base:   normal;


// Weights
$font-weight-light:     300;
$font-weight-normal:    400;
$font-weight-medium:    500;
$font-weight-semibold:  600;
$font-weight-bold:      700;
$font-weight-black:     800;

$font-weight-base:      $font-weight-normal;