/******************************************************************
VARIABLES - EFFECTS
******************************************************************/

// Shadows
$shadow01:              0 4px 20px rgba(0, 0, 0, 0.1);
$shadow02:              0 4px 36px rgba(0, 0, 0, 0.08);
$shadow03:              0 16px 30px rgba(0, 0, 0, 0.08);


// Beziers
$bezier01:              cubic-bezier(.65, .01, .4, .99);


// Borders
$border-radius:         8px;


// Transitions
$trans-time:            0.4s;
$trans-type:            ease;

$trans-time-sm:         0.24s;
$trans-time-md:         0.56s;
$trans-time-lg:         0.8s;

$transition-default:    all $trans-type $trans-time;
$transition01:          all $trans-time $bezier01;
$transition02:          all $trans-time-sm $bezier01;
$transition03:          all 0.64s $bezier01;